.TeamsCreateModalCustomCss {
  width: 496px !important;
  height: 100%;
  position: absolute !important;
  right: 0;
  margin-top: 0 !important;
  margin-bottom: 0 !important;

  .modal-content {
    height: 100%;
  }

  .modal-header {
    height: 112px;

    button {
      top: 40px;
      position: absolute;
      right: 24px;

      span {
        font-size: 30px;
      }
    }
  }

  .modal-footer {
    height: 96px;

    @media (max-width: 576px) {
      height: auto;
    }
  }

  .modal-title {
    width: 100%;
    height: 32px;
    top: 40px;
    position: fixed;
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
    margin-left: 24px;
  }
}

